.icons{
    display: flex;
    align-items: center;
    padding: 1rem;
    left: 0;
    position: sticky;
    text-decoration: none;
}
.icons_name:link {
    text-decoration: none;
  }
.icons:hover{
    background-color: rgb(206, 206, 206);
    cursor: pointer;
}
.icons:hover>.icons_img{
    color: red;
}
.icons_name{
    margin-left: 16px;
    flex: 1;
    color: rgb(116, 116, 116);
}
.icons_img{
    margin-left: 9px;
    color: rgb(116, 116, 116);
}
