.header{
    display: flex;
    padding: .3rem;
    margin: auto;
    justify-content: space-between;
    width: 99vw;
}

.header_left{
    display: flex;
    align-items: center;
}
.header_leftt{
    margin-left: 10px;
}
.header_left>img{
    object-fit: contain;
    height: 19px;
}

.header_center{
    display: flex;
    align-items: center;
    flex: .6;
}
.header_centerr{
    margin-left: 10px;
}
.header_center>input{
    flex: 1;
    padding: .4rem;
}

.header_right{
    display: flex;
    align-items: center;
}
.header_rightt{
    margin-right: 15px;
}